'use client';

import { US_STATES } from '@/types/agency';
import { useState } from 'react';

interface USMapProps {
  onStateClick: (stateCode: string) => void;
}

export default function USMap({ onStateClick }: USMapProps) {
  const [hoveredState, setHoveredState] = useState<string | null>(null);

  return (
    <div className="w-full max-w-7xl mx-auto px-4 py-6">
      <div className="text-center mb-8">
        <h2 className="text-3xl font-bold text-gray-900 mb-3">
          Medicare Agency Finder
        </h2>
        <p className="text-lg text-gray-600">
          Select a state to view Medicare-enrolled Home Health Care Agencies
        </p>
      </div>
      
      {/* Interactive US Map using SVG with CSS styling */}
      <div className="flex justify-center items-center">
        <div className="relative w-full" style={{ maxWidth: '1200px' }}>
          <svg 
            xmlns="http://www.w3.org/2000/svg"
            viewBox="0 0 959 593"
            className="w-full h-auto"
          >
            <defs>
              <style>{`
                .state {
                  fill: #1e3a8a;
                  stroke: #ffffff;
                  stroke-width: 0.75;
                  stroke-opacity: 1;
                  transition: fill 0.2s ease;
                  cursor: pointer;
                }
                .state:hover {
                  fill: #000000;
                }
              `}</style>
            </defs>
            
            {/* We'll use the SVG as a background image and overlay interactive regions */}
            <image 
              href="/Blank_US_Map.svg" 
              width="959" 
              height="593"
              style={{ pointerEvents: 'none' }}
            />
            
            {/* Transparent overlay for interaction */}
            <g>
              {Object.keys(US_STATES).map((stateCode) => (
                <path
                  key={stateCode}
                  id={stateCode}
                  className="state"
                  style={{
                    fill: hoveredState === stateCode ? '#000000' : 'transparent',
                    stroke: 'transparent',
                    strokeWidth: 0
                  }}
                  onMouseEnter={() => setHoveredState(stateCode)}
                  onMouseLeave={() => setHoveredState(null)}
                  onClick={() => onStateClick(stateCode)}
                />
              ))}
            </g>
            
            {/* Borders and labels on top */}
            <image 
              href="/Blank_US_map_borders_labels.svg" 
              width="959" 
              height="593"
              style={{ pointerEvents: 'none' }}
            />
          </svg>
        </div>
      </div>
      
      <div className="text-center mt-4 text-sm text-gray-500">
        {hoveredState ? `Hover: ${US_STATES[hoveredState as keyof typeof US_STATES]} (${hoveredState})` : 'Hover over a state'}
      </div>
    </div>
  );
}
